import { Injectable } from '@angular/core';
import {HttpClient,HttpClientModule } from '@angular/common/http';

@Injectable({
  providedIn: 'root'
})
export class HttpService {

  constructor(private http: HttpClient) { }

  // create http get request to Open Brewery
  getBreweries(cityZip?:string){

    return this.http.get(`https://api.openbrewerydb.org/breweries?by_postal=${cityZip}`)
  }

  getZipInfo(lat,long){
    return this.http.get(`http://localhost:8000/api/getZip?lat=${lat}&long=${long}`)
  }

  getMap(lat,long){
    return this.http.get(`http://localhost:8000/api/getMap?lat=${lat}&long=${long}`)
  }
}
