import { Component, OnInit, Input } from '@angular/core';
import {HttpService} from '../http.service';
import { DomSanitizer, SafeResourceUrl, SafeUrl} from '@angular/platform-browser';
import { FormBuilder, FormsModule } from '@angular/forms'



@Component({
  selector: 'app-home',
  template: `
  <div class='bg'>
    <div class="jumbotron shadow-lg p-3">
      <h2 class="display-3">Local Breweries</h2><br/>
    </div>
    <div>
    <div class=" zip-form">
      <form class="form-inline rounded" [formGroup]="cityZipForm" (ngSubmit)="getZip()" style="background-color:#dae3e3; margin-right:35%; margin-bottom:10px; margin-left:35%; padding:25px; border:2px solid black">
        <label for="cityZipInput" style="margin:auto; font-weight:400">
          SEARCH BY ZIP CODE: <input type="number" class="form-control rounded" name="cityZipInput" id='cityZipInput' formControlName="zip" style="border:2px solid black; margin-left:3px">
        </label>
        <button class="btn btn-outline-primary shadow p-3 rounded" id="zipBtn" style="margin:auto; padding-left:5px" type="submit" [disabled]="!cityZipForm.valid">Submit</button>
      </form>
    </div>
    <h4 style="text-align:center; margin-top:10px">OR</h4>
    <div>
      <button class = "btn btn-outline-primary shadow p-3 rounded" id='findUserButton'style=" margin-top:10px; margin-left:44%; margin-bottom:10px" (click)="findMe()">Find locations near me!</button>
      <h6 style="margin-top: 10px; margin-left:25%"> Not seeing any breweries? There may not be any near you, try searching another zip code</h6>
    </div>
    <div class="container float-right" >
      <div class="card text-white bg-dark w-50" style="margin-top:10px;margin-bottom:10px" *ngFor="let brewery of breweries">
          <h3 class="card-header text-center" style="margin:auto"><img src="/assets/img/beerGlass.png" style="width:30px;height:30px;" alt="beer glass"> {{brewery.name}}</h3>
          <div class="card-body">
            <h5 class="card-title" *ngIf='brewery.street != ""; else elseBlock'>{{brewery.street}}</h5>
            <ng-template #elseBlock>ADDRESS INFORMATION IS NOT AVAILABLE</ng-template>
            <h6 class="card-subtitle" *ngIf='brewery.street != ""'>{{brewery.city}}, {{brewery.postal_code}}</h6><br/>
            <svg  id="mapMarkerImage" style="vertical-align:center;"width="3em" height="3em" viewBox="0 0 16 16" class="bi bi-geo-alt" fill="currentColor" xmlns="http://www.w3.org/2000/svg" (click)="showMap(brewery.id)" *ngIf='brewery.longitude && brewery.latitude != null'>
              <path fill-rule="evenodd" d="M8 16s6-5.686 6-10A6 6 0 0 0 2 6c0 4.314 6 10 6 10zm0-7a3 3 0 1 0 0-6 3 3 0 0 0 0 6z"/>
            </svg>
            <span id='spanClick'(click)="showMap(brewery.id)" *ngIf="brewery.longitude && brewery.latitude !=null">show/hide map</span>
            <br/>
            <br/>
          <div *ngIf="brewery.longitude !=null; else mapElse">
            <img [src]="brewery.imageURL" class="rounded" id="{{brewery.id}}" style="display:none; margin:auto;" alt="brewery location on map" >
          </div>
          <ng-template #mapElse>MAP INFORMATION IS NOT AVAILABLE</ng-template><br/><br/>
          <a href="{{brewery.website_url}}" target="_blank">Visit Website</a>
        </div>
    </div>
</div>
  `,
  styleUrls: ['../../../node_modules/bootstrap/dist/css/bootstrap.css']
})
export class HomeComponent implements OnInit {

  breweries= new Array();
  cityZip;
  cityZipForm;

  constructor(
    public _http: HttpService,
    private sanitizer: DomSanitizer,
    private formBuilder: FormBuilder) {
    this.cityZipForm = this.formBuilder.group({
      zip: {type: Number}
    })
   }

  ngOnInit(): void {

  }

  findMe(){
    window.navigator.geolocation.getCurrentPosition((position) => {
      this._http.getZipInfo(position.coords.latitude, position.coords.longitude).subscribe(data => {
        this.fetchBrewery(JSON.parse(JSON.stringify(data)))
      })
    }, (error) => {
      console.log(error.message)
    })
  }

//send GET request to OpenBrewery DB and store response in breweries
  fetchBrewery(cityData?:string){
    this._http.getBreweries(cityData).subscribe(data => {
      //parse response data and store into breweries
      this.breweries = JSON.parse(JSON.stringify(data))
      this.breweries.forEach(element => {
        //Get request to LocationIQ
        this._http.getMap(element.latitude,element.longitude).subscribe(data => {
          //convert string_char response into base64string to covert into data URL
          let string_char = JSON.parse(JSON.stringify(data))
          let base64string = btoa(string_char)
          //implement domSanitizer to mark data URL as safe
          element.imageURL = this.sanitizer.bypassSecurityTrustUrl('data:image/png;base64,' + base64string)
        })
        element.postal_code = element.postal_code.slice("0",-5)
      })
    })
  }
//show map on user interaction
  showMap(name: string){
    let map = document.getElementById(name);
    if(map.style.display === "none"){
      map.style.display="block"
    } else {
      map.style.display="none"
    }
  }

//send new GET request to OpenBrewery DB
   getZip(){
    this.cityZip = this.cityZipForm.value.zip
    this.fetchBrewery(this.cityZip)
    this.cityZipForm.reset()
  }
}

